import './App.css';
import React,{Component} from 'react';
class App extends Component{
  constructor(props){
    super(props);
    this.state={
      count:0
    }
  }
  increment=()=>{
    this.setState({count:this.state.count +1});
  }
  decrement=()=>{
    if(this.state.count===0){
      this.setState({
        count:0
      });
    }
      else{
        this.setState(prevState=>({
        count:prevState.count-1
      }));
  }
}
  reset=()=>{
    this.setState({count:this.state.count=0})
  }
render(){
  return (
    <>
     <h4>React_Counter_App</h4>  
    <div className="counter">  
      <button className="btn btn-danger"onClick={this.decrement}>-</button>
      <h3>{this.state.count}</h3>
      <button className="btn btn-primary" onClick={this.increment}>+</button>
    </div>
    <button className="btn btn-warning p-2 my-2 text-uppercase text-dark"type="reset" onClick={this.reset}>reset</button>
    </>
  );
}
}
export default App;
